@mixin swipeIcon() {
    &::before {
        position: absolute;

        inset-block-start: var(--ym-spacer-size-xs);
        inset-inline-start: 50%;

        transform: translateX(-50%);

        border-radius: var(--ym-radius-size-xxxl);
        background-color: var(--ym-controls-color-primary-text-enabled);

        width: 45px;
        height: 4px;

        content: '';
    }
}
