@import '~@yandex-music-int/sol/mixins/aspectRatio.scss';

.button {
    border: 2px solid transparent;

    &_isSelected {
        border-color: var(--ym-controls-color-primary-outline-selected_stroke);
    }

    &_isApplying {
        animation: applying-setting 1s linear infinite;

        @media screen and (prefers-reduced-motion: reduce) {
            animation: none;
        }
    }

    @keyframes applying-setting {
        50% {
            border-color: var(--ym-controls-color-primary-outline-hovered_stroke);
        }
    }
}

.diversityButton {
    display: flex;
    flex: 1;
    flex-direction: column;
    align-items: center;
    gap: var(--ym-spacer-size-s);

    padding-block-start: var(--ym-spacer-size-l);
    padding-block-end: var(--ym-spacer-size-l);
}

.moodEnergy {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: var(--ym-spacer-size-xxs);
}

.moodEnergyButton {
    padding: var(--ym-spacer-size-xxxs);

    &:not(:disabled) {
        &:hover,
        &:focus-visible {
            transform: scale(1.1);
        }
    }
}

.textButton {
    padding-block-start: var(--ym-spacer-size-m);
    padding-block-end: var(--ym-spacer-size-m);
    padding-inline-start: var(--ym-spacer-size-xl);
    padding-inline-end: var(--ym-spacer-size-xl);
}

.title {
    color: var(--ym-controls-color-primary-on_outline-enabled);
}
