@import '~@yandex-music-int/sol/mixins/breakpoints.scss';
@import '~@yandex-music-int/sol/mixins/swipeIcon.scss';
@import '~@yandex-music-int/sol/mixins/scrollbar.scss';

.root {
    display: flex;
    flex-direction: column;
    align-items: center;

    box-shadow: 0 1px 24px 0 var(--ym-shadow-xl), 0 -1px 24px 0 var(--ym-shadow-s);
    border: 1px solid var(--ym-outline-color-primary-disabled);
    border-radius: var(--ym-radius-size-xl);
    background-color: var(--ym-background-color-primary-enabled-popover);

    padding-block-start: var(--ym-spacer-size-m);
    padding-block-end: var(--ym-spacer-size-m);
    padding-inline-start: var(--ym-spacer-size-m);
    padding-inline-end: var(--ym-spacer-size-m);

    backdrop-filter: saturate(180%) blur(15px);

    height: 100%;

    overflow-y: auto;
    scroll-snap-type: y mandatory;

    @include setScrollbarVisibility($visible: true);
}

.popover {
    position: absolute;
    z-index: var(--ym-z-index-popover);

    width: 100%;
    max-width: 410px;

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        position: fixed;

        border-end-end-radius: 0;
        border-end-start-radius: 0;

        width: 100vw;
        max-width: 100vw;
    }
}

.overlay {
    // FloatingOverlay инлайнит overflow: auto; что мешает сделать анимацию настроек
    overflow: visible !important;
}

.toggleSettingsButton {
    flex-shrink: 0;
}

.header {
    display: flex;

    align-items: center;
    justify-content: space-between;

    margin-block-end: var(--ym-spacer-size-xl);

    width: 100%;

    color: var(--ym-controls-color-primary-text-enabled_variant);

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        padding-inline-start: var(--ym-spacer-size-xs);
        padding-inline-end: var(--ym-spacer-size-xs);

        @include swipeIcon();
    }
}

.actions {
    display: flex;
    align-items: center;
    gap: var(--ym-spacer-size-s);
}

.settings {
    display: flex;
    flex-direction: column;
    gap: var(--ym-spacer-size-xl);

    width: 100%;

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        padding-inline-start: var(--ym-spacer-size-xs);
        padding-inline-end: var(--ym-spacer-size-xs);
    }
}

.diversity {
    justify-content: space-evenly;
}

.moodEnergy {
    gap: var(--ym-spacer-size-xxl);
}

.contextItems {
    flex-wrap: wrap;
}
