@import '~@yandex-music-int/sol/mixins/breakpoints.scss';
@import '~@yandex-music-int/sol/mixins/firefoxStyles.scss';

.root {
    padding: 0;
}

.content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    padding: 0;

    height: 100%;
}

.container {
    flex-grow: 1;
}

.myVibe {
    position: relative;

    height: 56vh;
    min-height: 500px;
}

.landing {
    display: flex;

    position: relative;
    z-index: 1;
    flex-direction: column;

    margin-block-start: -8vh;
    gap: var(--ym-spacer-size-l);
}

.beta {
    position: absolute;
    inset-block-start: var(--ym-spacer-size-m);
    inset-inline-start: var(--ym-spacer-size-m);

    z-index: var(--ym-z-index-header);

    border-radius: var(--ym-radius-size-xl);
    background-color: var(--ym-controls-color-secondary-default-enabled);
    backdrop-filter: saturate(180%) blur(15px);

    padding-block-start: var(--ym-spacer-size-xxxs);
    padding-block-end: var(--ym-spacer-size-xxxs);
    padding-inline-start: var(--ym-spacer-size-xs);
    padding-inline-end: var(--ym-spacer-size-xs);
}

.footer {
    padding-block-start: var(--ym-spacer-size-xxxl);
    padding-block-end: var(--ym-spacer-size-xxl);
    padding-inline-start: var(--ym-spacer-size-xxl);
    padding-inline-end: var(--ym-spacer-size-s);

    @include firefoxStyles() {
        padding-inline-end: var(--ym-spacer-size-l);
    }

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        padding-block-start: var(--ym-spacer-size-l);
        padding-block-end: var(--ym-spacer-size-xxxl);
        padding-inline-start: var(--ym-spacer-size-l);
    }
}

// TODO: убрать дополнительный селектор после фикса https://st.yandex-team.ru/MFRONT-5960
.error.important {
    height: initial;

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        margin-block-end: var(--ym-spacer-size-xxxxl);
    }
}
