@import '~@yandex-music-int/sol/mixins/breakpoints.scss';

.root {
    display: flex;
    align-items: center;
    justify-content: center;

    gap: var(--ym-spacer-size-l);
}

.context {
    display: flex;
    align-items: center;
    gap: var(--ym-spacer-size-xxxs);

    border-radius: var(--ym-radius-size-xxxl);

    background-color: var(--ym-controls-color-secondary-default-enabled);

    // TODO: заменить на токен в рамках https://st.yandex-team.ru/MFRONT-5950
    padding-block-start: 14px;
    padding-block-end: 14px;
    padding-inline-start: var(--ym-spacer-size-xxl);
    padding-inline-end: var(--ym-spacer-size-l);

    backdrop-filter: saturate(180%) blur(15px);

    max-width: 100%;

    color: var(--ym-controls-color-secondary-on_default-enabled);

    @include mediaBreakpointDown(380, false) {
        max-width: 80%;
    }
}

.resetButton {
    flex-shrink: 0;

    outline: none;

    color: var(--ym-controls-color-secondary-on_default-enabled_variant);
    fill: var(--ym-controls-color-secondary-on_default-enabled_variant);

    &:not(:disabled) {
        &:hover,
        &:focus-visible {
            box-shadow: none;

            color: var(--ym-controls-color-secondary-on_default-hovered);
            fill: var(--ym-controls-color-secondary-on_default-hovered);
        }
    }
}
