@import './breakpoints';

@mixin setScrollbarVisibility($visible: true) {
    transition: scrollbar-color 200ms ease-in-out;

    // WebKit supported browsers (Chrome, YaBro, Safari, etc.)
    &::-webkit-scrollbar-thumb {
        border: 8px solid transparent;
        border-radius: var(--ym-radius-size-l);

        background-clip: content-box;

        min-height: 50px;
    }

    &::-webkit-scrollbar {
        width: 24px;
    }

    @if $visible {
        // FireFox
        scrollbar-color: var(--ym-surface-color-primary-enabled-entity) transparent;

        // WebKit supported browsers (Chrome, YaBro, Safari, etc.)
        &::-webkit-scrollbar-thumb {
            background-color: var(--ym-controls-color-primary-default-disabled);
        }

        // stylelint-disable-next-line a11y/selector-pseudo-class-focus
        &::-webkit-scrollbar-thumb:hover {
            background-color: var(--ym-surface-color-primary-enabled-entity);
        }
        /* stylelint-disable-next-line at-rule-empty-line-before */
    } @else {
        // Переменная, чтобы скрывать скроллбар через альфа-канал
        --scrollbar-hidden: rgba(0, 0, 0, 0);

        // FireFox
        scrollbar-color: var(--scrollbar-hidden) transparent;
        scrollbar-gutter: stable;

        // WebKit supported browsers (Chrome, YaBro, Safari, etc.)
        &::-webkit-scrollbar-thumb {
            background-color: var(--scrollbar-hidden);
        }
    }

    @media (prefers-reduced-motion: reduce) {
        transition: none;
    }

    @include mediaBreakpointDown($width: 1024) {
        &::-webkit-scrollbar-thumb {
            border: 3px solid transparent;
        }

        &::-webkit-scrollbar {
            width: var(--ym-spacer-size-m);
        }
    }
}
