@import '~@yandex-music-int/sol/mixins/fontStyles.scss';
@import '~@yandex-music-int/sol/mixins/breakpoints.scss';

.root {
    background-clip: text;

    background-image: var(--ym-controls-color-primary-text-vibe);

    background-repeat: repeat;
    background-size: 100%;

    color: transparent;

    &_isLoading {
        animation: applying-setting 1s linear infinite;

        @media screen and (prefers-reduced-motion: reduce) {
            animation: none;
        }
    }

    &:not(:disabled) {
        &:hover,
        &:focus-visible {
            transform: scale(1.05);

            box-shadow: none;
        }
    }

    @include fontStyles(
        $font-family: var(--ym-font-heading),
        $font-size: var(--ym-font-size-vibe-s),
        $line-height: var(--ym-font-line-height-vibe),
        $font-weight: var(--ym-font-weight-black),
        $letter-spacing: var(--ym-letter-spacing-vibe)
    );

    @include mediaBreakpointUp($ym-breakpoint-xl) {
        @include fontStyles(
            $font-family: var(--ym-font-heading),
            $font-size: var(--ym-font-size-vibe-m),
            $line-height: var(--ym-font-line-height-vibe),
            $font-weight: var(--ym-font-weight-black),
            $letter-spacing: var(--ym-letter-spacing-vibe)
        );
    }
}

.icon {
    margin-inline-end: var(--ym-spacer-size-xs);

    color: var(--ym-controls-color-primary-text-vibe_icon);

    &_pauseVibe {
        --ym-icon-padding: 2px 4px 4px 5px;
    }

    &_playVibe {
        --ym-icon-padding: 0 0 1px 6px;
    }

    @include mediaBreakpointUp($ym-breakpoint-xl) {
        width: var(--ym-spacer-size-xxxxl);
        height: var(--ym-spacer-size-xxxxl);
    }
}

@keyframes applying-setting {
    50% {
        opacity: 0.5;
    }
}
