@import '~@yandex-music-int/sol/mixins/breakpoints.scss';

.root {
    display: flex;
    flex-direction: column;
    gap: var(--ym-spacer-size-m);

    width: 100%;
}

.restrictions {
    display: flex;
    justify-content: flex-start;
    gap: var(--ym-spacer-size-m);

    width: 100%;

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        flex-wrap: wrap;
        gap: var(--ym-spacer-size-xxs);
    }

    @include mediaBreakpointDown(400, false) {
        justify-content: flex-start;
    }
}
