.root {
    &_visible {
        will-change: opacity;
        animation: fade-in 2s ease-in-out;

        @media screen and (prefers-reduced-motion: reduce) {
            animation: none;
        }
    }
}

@keyframes fade-in {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}
