@import '~@yandex-music-int/sol/mixins/breakpoints.scss';

.root {
    display: flex;
    position: relative;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.animation {
    position: absolute;
    inset-block-start: 50%;
    inset-inline-start: 50%;

    transform: translate3d(-50%, -50%, 0);

    [dir='rtl'] & {
        transform: translate3d(50%, -50%, 0);
    }
}

.controls {
    display: flex;

    position: relative;

    flex-direction: column;
    align-items: center;

    min-width: 370px;

    @include mediaBreakpointDown(900) {
        min-width: 350px;
    }

    @include mediaBreakpointDown(550) {
        min-width: 300px;
    }

    @include mediaBreakpointDown(400) {
        min-width: 250px;
    }
}

.settingsButton {
    position: absolute;

    inset-block-start: 100%;

    padding-block-start: var(--ym-spacer-size-xl);

    width: 100%;
}
