/* stylelint-disable scale-unlimited/declaration-strict-value */
@mixin fontStyles(
    $font-family,
    $font-size,
    $line-height: normal,
    $font-weight: normal,
    $font-style: normal,
    $letter-spacing: normal
) {
    line-height: $line-height;
    letter-spacing: $letter-spacing;
    font-family: $font-family;
    font-size: $font-size;
    font-weight: $font-weight;
    font-style: $font-style;
}
